          console.error('üìç [ËØ∑Ê±ÇURL]', `${config.baseURL}${config.API.CHALLENGE.LIST}`);
          console.error('üí° [Ëß£ÂÜ≥ÊñπÊ°à]');
          console.error('    1. Ê£ÄÊü•ÂêéÁ´ØÊòØÂê¶Â∑≤ÂÆûÁé∞Ê≠§Êé•Âè£');
          console.error('    2. Á°ÆËÆ§Êé•Âè£Ë∑ØÂæÑÊòØÂê¶Ê≠£Á°ÆÔºàÂΩìÂâç: /api/challenge/tasksÔºâ');
          console.error('    3. ËÅîÁ≥ªÂêéÁ´ØÂºÄÂèëÁ°ÆËÆ§Êé•Âè£ÊòØÂê¶Â∑≤ÈÉ®ÁΩ≤');
          console.error('    4. Â¶ÇÊûúÊòØË∑ØÂæÑÈóÆÈ¢òÔºåÂèØËÉΩÈúÄË¶Å‰øÆÊîπ utils/config.js ‰∏≠ÁöÑÈÖçÁΩÆ');
          console.error('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
        } else if (statusCode === 401 || errorMsg.includes('401')) {
          console.error('üîê [ÈîôËØØÁ±ªÂûã] Êú™ÊéàÊùÉ (401)');
          console.error('üí° [Ëß£ÂÜ≥ÊñπÊ°à] TokenÂèØËÉΩÂ∑≤ËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï');
        } else if (errorMsg.includes('timeout')) {
          console.error('‚è±Ô∏è [ÈîôËØØÁ±ªÂûã] ËØ∑Ê±ÇË∂ÖÊó∂');
          console.error('üí° [Ëß£ÂÜ≥ÊñπÊ°à] Ê£ÄÊü•ÁΩëÁªúËøûÊé•ÊàñÂêéÁ´ØÊúçÂä°ÊòØÂê¶Ê≠£Â∏∏');
