    async toggleFavorite(item) {
      const newFavoriteState = !item.favorite;
      const action = newFavoriteState ? 'Êî∂Ëóè' : 'ÂèñÊ∂àÊî∂Ëóè';
      
      console.log(`‚≠ê [‰∏ÄÁôæ‰ª∂‰∫ã] ========== ${action}‰ªªÂä° ==========`);
      console.log('üìã [‰ªªÂä°] ID:', item.id, 'ÂêçÁß∞:', item.text);
      console.log('üîÑ [Áä∂ÊÄÅ] ÂΩìÂâç:', item.favorite ? 'Â∑≤Êî∂Ëóè' : 'Êú™Êî∂Ëóè', '‚Üí Êñ∞Áä∂ÊÄÅ:', newFavoriteState ? 'Â∑≤Êî∂Ëóè' : 'Êú™Êî∂Ëóè');
      
      // ÂÖàÊõ¥Êñ∞Êú¨Âú∞Áä∂ÊÄÅÔºà‰πêËßÇÊõ¥Êñ∞Ôºâ
