      this.editForm = null;
    },
    /**
     * ‰øùÂ≠òÁºñËæë
     * Ê≥®ÊÑèÔºöÂêéÁ´ØÂèØËÉΩ‰∏çÊîØÊåÅÁºñËæëÈ¢ÑËÆæ‰ªªÂä°ÔºåÂè™ÊîØÊåÅËá™ÂÆö‰πâ‰ªªÂä°
     */
    async saveEdit() {
      if (!this.editForm.text) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÂÜÖÂÆπ', icon: 'none' });
        return;
      }
      
      console.log('‚úèÔ∏è [‰∏ÄÁôæ‰ª∂‰∫ã] ========== ‰øùÂ≠òÁºñËæë ==========');
      console.log('üìã [‰ªªÂä°] ID:', this.editForm.id);
      console.log('üìù [ÂÜÖÂÆπ] Êóß:', this.items.find(i => i.id === this.editForm.id)?.text);
      console.log('üìù [ÂÜÖÂÆπ] Êñ∞:', this.editForm.text);
      
      const index = this.items.findIndex(item => item.id === this.editForm.id);
      if (index !== -1) {
        const oldText = this.items[index].text;
        
        // ÂÖàÊõ¥Êñ∞Êú¨Âú∞
        this.items[index].text = this.editForm.text;
        this.saveItemsToLocal();
        this.closeEdit();
        
        // Â¶ÇÊûúÊòØËá™ÂÆö‰πâ‰ªªÂä°ÔºåÂêåÊ≠•Âà∞ÂêéÁ´ØÔºàÈ¢ÑËÆæ‰ªªÂä°ÂèØËÉΩ‰∏çÊîØÊåÅÁºñËæëÔºâ
        const item = this.items[index];
        if (item.category === 'custom') {
          try {
            console.log('üì° [ÂâçÁ´Ø] Ëá™ÂÆö‰πâ‰ªªÂä°ÔºåÂ∞ùËØïÂêåÊ≠•Âà∞ÂêéÁ´Ø...');
            // Ê≥®ÊÑèÔºöÂêéÁ´ØÂèØËÉΩÊ≤°ÊúâÁºñËæëÊé•Âè£ÔºåËøôÈáåÂÖàÂè™‰øùÂ≠òÊú¨Âú∞
            // Â¶ÇÊûúÈúÄË¶ÅÂêéÁ´ØÊîØÊåÅÔºåÂèØ‰ª•Ê∑ªÂä†ÁºñËæëÊé•Âè£
            console.log('‚ÑπÔ∏è [ÊèêÁ§∫] ÂΩìÂâçÂêéÁ´ØÂèØËÉΩ‰∏çÊîØÊåÅÁºñËæëÊé•Âè£Ôºå‰ªÖ‰øùÂ≠òÂà∞Êú¨Âú∞');
            uni.showToast({ title: 'Â∑≤‰øùÂ≠òÔºà‰ªÖÊú¨Âú∞Ôºâ', icon: 'success' });
          } catch (error) {
            console.error('‚ùå [ÂêéÁ´Ø] ÂêåÊ≠•Â§±Ë¥•:', error);
            // ÂõûÊªö
            this.items[index].text = oldText;
            this.saveItemsToLocal();
            uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', icon: 'none' });
          }
        } else {
          console.log('‚ÑπÔ∏è [ÊèêÁ§∫] È¢ÑËÆæ‰ªªÂä°‰ªÖ‰øùÂ≠òÂà∞Êú¨Âú∞');
        uni.showToast({ title: 'Â∑≤‰øùÂ≠ò', icon: 'success' });
        }
      }
    },
    confirmDelete(item) {
      uni.showModal({
        title: 'Âà†Èô§Á°ÆËÆ§',
        content: `Á°ÆÂÆöÂà†Èô§‚Äú${item.text}‚ÄùÂêóÔºü`,
        confirmText: 'Âà†Èô§',
        cancelText: 'ÂèñÊ∂à',
        success: (res) => { if (res.confirm) this.deleteItem(item); }
      });
    },
    /**
     * Âà†Èô§‰ªªÂä°
     * ÂêåÊ≠•Âà∞ÂêéÁ´ØÔºà‰ªÖËá™ÂÆö‰πâ‰ªªÂä°ÂèØÂà†Èô§Ôºâ
     */
    async deleteItem(item) {
      console.log('üóëÔ∏è [‰∏ÄÁôæ‰ª∂‰∫ã] ========== Âà†Èô§‰ªªÂä° ==========');
      console.log('üìã [‰ªªÂä°] ID:', item.id, 'ÂêçÁß∞:', item.text);
      console.log('üìÇ [Á±ªÂûã]', item.category || 'Êú™Áü•');
      
      // Âè™ÊúâËá™ÂÆö‰πâ‰ªªÂä°ÂèØ‰ª•Âà†Èô§
      if (item.category === 'custom') {
        try {
          console.log('üì° [ÂâçÁ´Ø] Ë∞ÉÁî® deleteTask() API');
          await deleteTask(item.id);
          
          console.log('‚úÖ [ÂêéÁ´Ø] Âà†Èô§‰ªªÂä°ÊàêÂäü');
          
          // ‰ªéÂàóË°®‰∏≠ÁßªÈô§
      this.items = this.items.filter(it => it.id !== item.id);
          this.saveItemsToLocal();
          
          uni.showToast({ title: 'Â∑≤Âà†Èô§', icon: 'success' });
        } catch (error) {
          console.error('‚ùå [ÂêéÁ´Ø] Âà†Èô§‰ªªÂä°Â§±Ë¥•:', error);
          uni.showToast({
            title: 'Âà†Èô§Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
            icon: 'none',
