          });
        }
      } else {
        console.log('‚ö†Ô∏è [Ë≠¶Âëä] È¢ÑËÆæ‰ªªÂä°‰∏çËÉΩÂà†Èô§');
        uni.showToast({
          title: 'È¢ÑËÆæ‰ªªÂä°‰∏çËÉΩÂà†Èô§',
          icon: 'none',
          duration: 2000
        });
      }
    },
    /**
     * ÂàáÊç¢ÂÆåÊàêÁä∂ÊÄÅ
     * ÂêåÊ≠•Âà∞ÂêéÁ´Ø
     */
    async toggleDone(item) {
      const newDoneState = !item.done;
      const action = newDoneState ? 'Ê†áËÆ∞ÂÆåÊàê' : 'ÂèñÊ∂àÂÆåÊàê';
      
      console.log(`‚úÖ [‰∏ÄÁôæ‰ª∂‰∫ã] ========== ${action}‰ªªÂä° ==========`);
      console.log('üìã [‰ªªÂä°] ID:', item.id, 'ÂêçÁß∞:', item.text);
