    async syncTaskComplete(item, completed, photoUrl = null) {
      console.log('üì° [ÂâçÁ´Ø] Ë∞ÉÁî® completeTask() API');
      console.log('üì§ [ÂèÇÊï∞] taskId:', item.id, 'completed:', completed, 'photoUrl:', photoUrl || 'Êó†');
      
      await completeTask({
        taskId: item.id,
        completed: completed,
        photoUrl: photoUrl || null,
        note: item.note || null
      });
    },
    openCatalog() { this.showCatalog = true; },
    closeCatalog() { this.showCatalog = false; },
    openAdd() { this.showAdd = true; },
    closeAdd() { this.showAdd = false; this.form.text = ''; },
    /**
     * ‰øùÂ≠òÊñ∞‰ªªÂä°
     * ÂêåÊ≠•Âà∞ÂêéÁ´Ø
     */
    async saveItem() {
      if (!this.form.text) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÂÜÖÂÆπ', icon: 'none' });
        return;
      }
      
      console.log('‚ûï [‰∏ÄÁôæ‰ª∂‰∫ã] ========== Ê∑ªÂä†Êñ∞‰ªªÂä° ==========');
      console.log('üìù [ÂÜÖÂÆπ]', this.form.text);
      
      const taskData = {
        taskName: this.form.text,
        taskDescription: ''
      };
      
      try {
        console.log('üì° [ÂâçÁ´Ø] Ë∞ÉÁî® addTask() API');
        const response = await addTask(taskData);
        
        console.log('‚úÖ [ÂêéÁ´Ø] Ê∑ªÂä†‰ªªÂä°ÊàêÂäü');
        console.log('üì¶ [ÂìçÂ∫î]', response);
        
        // Ê∑ªÂä†Êñ∞‰ªªÂä°Âà∞ÂàóË°®
        if (response && response.task) {
          const newTask = this.convertBackendToFrontend(response.task);
          this.items.unshift(newTask);
          console.log('‚úÖ [ÂâçÁ´Ø] Êñ∞‰ªªÂä°Â∑≤Ê∑ªÂä†Âà∞ÂàóË°®ÔºåID:', newTask.id);
        } else {
          // Â¶ÇÊûúÂêéÁ´ØÊ≤°ÊúâËøîÂõûÂÆåÊï¥‰ªªÂä°ÂØπË±°ÔºåÂàõÂª∫‰∏Ä‰∏™‰∏¥Êó∂ÂØπË±°
          console.warn('‚ö†Ô∏è [Ë≠¶Âëä] ÂêéÁ´ØÊú™ËøîÂõûÂÆåÊï¥‰ªªÂä°ÂØπË±°ÔºåÂàõÂª∫‰∏¥Êó∂ÂØπË±°');
          const maxId = this.items.reduce((m, it) => Math.max(m, it.id || 0), 0);
          const tempTask = {
            id: maxId + 1,
            text: this.form.text,
            done: false,
            favorite: false,
            image: '',
            category: 'custom'
          };
          this.items.unshift(tempTask);
        }
        
        this.saveItemsToLocal();
      this.closeAdd();
        uni.showToast({ title: 'Â∑≤Ê∑ªÂä†', icon: 'success' });
        
      } catch (error) {
        console.error('‚ùå [ÂêéÁ´Ø] Ê∑ªÂä†‰ªªÂä°Â§±Ë¥•:', error);
        uni.showToast({
          title: 'Ê∑ªÂä†Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
